.header {
  margin-bottom: 20px;
  &__container {
    height: 120px;
    background-color: #cf6161;
  }
}

@media all and(max-width: 768px) {
  .header {
    position: fixed;
    width: 100%;
    bottom: 0;
    left: 0;
    margin: 0;
    &__container {
      height: 60px;
    }
  }
}